body {
    background-color: rgb(250, 250, 250);
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 500;
  }
  
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; 
    background-color: ivory;
    
  }
  .image {
    width: 370px;
    margin-top: 50px;
  }
  @media (max-width: 770px) {
    .container {
      margin: 0 10%;
    }
    .image {
      width: 100%;
    }
  }